.logo {
	color: $bl;
	padding: 0.375rem 0;
	margin-right: 1.875rem;
	& &-img {
		&-hold {
			margin-right: 1.5rem; } }
	& &-text {
		margin-top: -0.125rem;
		&-part-1 {
			font-size: 1.1875rem; }
		&-part-2 {
			font-size: 0.625rem;
			text-transform: uppercase;
			color: rgba($bl, .7); } }
	&:hover {
		color: $orange; } }
@include sm-636 {
	.logo-img-hold {
		display: none; } }


.nav-header {
	position: fixed;
	width: 100%;
	transition {}
	padding: 2.5rem 0 3.125rem;
	transition: all .25s;
	z-index: 9;
	&.toggle-fixed-navbar {
		background-color: $wh;
		padding: 0.625rem 0;
		border-bottom: 0.0625rem solid #eee;
		.btn {
			.btn-icon-hold {
				// width: 2.75rem
 }				// height: 2.75rem
			.btn-icon-circle::before {
				// width: 3.375rem
 } } } }				// height: 3.375rem

@include sm-536 {
	.nav-header {
		.btn-contact {
			.btn-icon {
				display: none; } } } }

@include sm-480 {
	.nav-header {
		padding: 1.5625rem 0;
		position: absolute;
		.nav {
			flex-direction: column;
			.logo {
				text-align: center;
				margin-right: 0;
				&-text {
					margin-left: auto;
					margin-right: auto; } } } }
	.nav-padding {
		padding-top: 5rem; }


	.nav-header {
		.btn-contact.btn {
			margin-left: auto;
			margin-right: auto;
			padding: 0 0;
			opacity: 0;
			position: fixed;
			bottom: -4.375rem;
			left: 0;
			transition: all .3s;
			justify-content: center;
			width: 100%;
			.btn-text {
				background-color: #fff;
				padding: 0.9375rem 1.5rem 0.75rem 3.125rem;
				margin-left: -2rem;
				border-radius: 0.375rem; }
			.btn-icon {
				display: flex; } }
		&.toggle-fixed-navbar {
			.btn-contact.btn {
				bottom: 0.625rem;
				opacity: 1; } } } }
