// CONVERTING PX TO REM
@function rem($size, $default-size: $default-font-size) {
	@return ($size / $default-size) + rem; }


// Dark / Light mode
@mixin dark {
	body.dark {
		@content; } }


// Responsive breakpoints
@mixin r($width) {
	@media only screen and (max-width: $width+ "px") {
		@content; } }

@mixin rmin($width) {
	@media only screen and (min-width: $width+ "px") {
		@content; } }

@mixin xxl {
	@include r(1400) {
		@content; } }

@mixin xl {
	@include r(1200) {
		@content; } }

@mixin lg-1190 {
	@include r(1190) {
		@content; } }

@mixin lg {
	@include r(992) {
		@content; } }

@mixin lg-920 {
	@include r(920) {
		@content; } }

@mixin md {
	@include r(768) {
		@content; } }

@mixin sm-636 {
	@include r(636) {
		@content; } }

@mixin sm-536 {
	@include r(536) {
		@content; } }

@mixin sm-480 {
	@include r(480) {
		@content; } }
