.header {
	& &-wrapper {
		@include lg-1190 {
			flex-direction: column;
			text-align: center; }
		.btn-contact {
			display: none;
			@include sm-480 {
				display: flex;
				margin-left: auto;
				margin-right: auto;
				justify-content: center;
				padding-bottom: 3.125rem; } } }
	& &-text-hold {
		padding-top: 1.375rem; }
	& &-info {
		margin-bottom: 2.125rem;
		@include sm-480 {
			margin-bottom: 1.5625rem; }
		&-img {
			margin-right: 0.625rem; } }
	& &-title {
		font-size: 4.375rem;
		margin: 0 0 1.5rem;
		@include sm-480 {
			font-size: 3.4375rem; }
		&-smaller {
			font-size: 3.75rem;
			margin-bottom: 2.5rem;
			@include sm-480 {
				font-size: 2.9375rem; } } }
	& &-img {
		margin-right: 0.625rem;
		width: 100%;
		height: auto;
		&-hold {
			padding-left: 1.875rem; } } }




.page-wrapper {
	padding-top: 3.25rem;
	padding-bottom: 5.625rem; }

.page-holder {
	justify-content: space-between;
	margin-top: -7.5rem;
	@include lg-920 {
		margin-top: 0;
		flex-direction: column; } }

.page-sidebar {
	max-width: 17.1875rem;
	width: 100%;
	margin-right: 4.375rem;
	height: 100%;
	position: sticky;
	top: 0;
	padding-top: 7.5rem;
	transition: all .2s;
	@include lg-920 {
		padding-top: 0;
		margin: 3.125rem auto 0;
		order: 2;
		height: auto;
		position: relative; }
	.contact-item {
		padding-bottom: 3.125rem; } }

.page-content {
	padding-top: 8.4375rem;
	width: 100%;
	max-width: 48rem;
	@include lg-920 {
		padding-top: 0.9375rem;
		max-width: 100%; } }





.card-margin {
	margin: 4.0625rem 0;
	&:first-child {
		margin-top: 0; }
	&:last-child {
			margin-bottom: 0; } }

.card-content {
	line-height: 1.85; }

.card-info {
	background-color: var(--card-bg);
	border-radius: 0.625rem;
	padding: 2rem 2.375rem; }


.card-list {
	& &-title {
		margin-bottom: 3.75rem; }
	& &-holder {}
	& &-item {
		position: relative;
		padding-left: 5.9375rem;
		margin-bottom: 3.25rem;
		min-height: 3.75rem;
		&:last-child {
			margin-bottom: 0; }
		&-icon {
			width: 3.75rem;
			height: 3.75rem;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: var(--card-icon-bg);
			color: var(--card-icon-text);
			&-hold {
				position: absolute;
				top: 0.1875rem;
				left: 0; }
			.icon {
				width: 1.75rem;
				height: 1.75rem; } }
		&-title {
			font-size: 1.5rem;
			margin-bottom: 0.9375rem; }
		.card-content {
			font-size: 1.125rem;
			line-height: 1.75; } } }




.card-comments {
	margin-top: 5.75rem;
	.title {
		align-items: center;
		&-img {}
		&-part {
			padding-left: 2rem;
			&-lg {
				font-size: 2.1875rem; }
			&-sm {
				font-size: 1.5rem;
				margin-top: 0.625rem; } } } }


.modal-backdrop {
	--bs-backdrop-bg: #000;
	--bs-backdrop-opacity: .6; }

.modal-action {
	.btn-close {
		position: absolute;
		top: 1.0625rem;
		right: 1.0625rem;
		z-index: 9;
		background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M.293.293a1 1 0 0 1 1.414 0L8 6.586 14.293.293a1 1 0 1 1 1.414 1.414L9.414 8l6.293 6.293a1 1 0 0 1-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 0 1-1.414-1.414L6.586 8 .293 1.707a1 1 0 0 1 0-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
		opacity: .7;
		transition: all .3s;
		&:hover {
			opacity: 1; } }
	.modal-dialog {
		--bs-modal-width: 59.6875rem; }
	.modal-body {
		padding: 2.1875rem; }
	.modal-content {
		min-height: 35rem;
		box-shadow: 0 0 0 0.625rem rgba($wh, .08);
		background-repeat: no-repeat;
		background-size: cover;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba($gray-3, .56); } }
	.modal-hold {
		color: $wh;
		width: 100%;
		.title {
			margin-bottom: 0.9375rem; }
		.btn-hold {
			margin-top: 0.625rem; }
		.box-info {
			.required::before {
				color: #ff6353; } } } }



.common-issue {
	&-input {
		background-color: $gray;
		border-radius: 0.25rem;
		border: 0.0625rem solid transparent;
		padding-left: 3.75rem;
		&:focus {
			border: 0.0625rem solid transparent; }
		&-hold {
			margin-bottom: 1.875rem;
			.icon {
				left: 1.25rem;
				color: $bl; } } }
	&-title {
		font-size: 1.25rem;
		margin-bottom: 1.75rem; }
	& &-item {
		position: relative;
		margin-bottom: 1.125rem;
		padding-left: 2.5rem;
		.collapse-icon {
			position: absolute;
			top: 0.5rem;
			left: 0;
			color: $blue-2; }
		&-label {
			font-size: 1rem;
			color: $blue;
			font-weight: 600;
			display: flex;
			flex-direction: column;
			.item-label-1 {}
			.item-label-2 {
				font-size: 0.875rem;
				font-weight: 500;
				color: $gray-4; } }
		.collapse-content {
			border-radius: 0.375rem;
			padding: 0.9375rem 1.25rem;
			margin-top: 0.625rem;
			background: $gray-2; }
		&--empty {
			display: flex;
			justify-content: start;
			.icon {
				color: #de6d62;
				margin-right: 0.75rem;
				min-width: 1.5rem; } } } }



