 :root {
	--base-fz : $default-font-size;
	--base-tc : $default-text-color;
	--base-pl : $default-placeholder-color;
	--card-bg : #f3fefd;
	--card-icon-bg : #b2d3a1;
	--card-icon-text : #fff; }

*, ::before, ::after {
	box-sizing: border-box; }

html,
body {
	height: 100%; }
body {
	font-family: 'Montserrat', sans-serif;
	color: $default-text-color;
	font-weight: 500; }

svg {
	position: relative;
	width: 1.5rem;
	height: 1.5rem; }

a,
button,
.pointer,
.btn, {
	cursor: pointer;
	text-decoration: none; }


a,
a:focus,
a:focus-within,
a:active,
a:hover,
button,
button:focus,
button:focus-within,
.btn,
.btn:hover,
.btn:focus,
.btn:active {
	outline: none !important;
	border: none; }

a {
	color: $pr;
	&:active {
		color: $pr; }
	&:hover {
		color: $pr-h; } }

.animate::before,
.animate::after,
.animate {
	transition: all .2s; }

.text-limit span,
.text-limit {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	display: inline-block;
	max-width: 100%; }

.form-control::-webkit-input-placeholder,
.form-control::-moz-placeholder,
.form-control:-ms-input-placeholder,
.form-control::placeholder {
	color: $default-placeholder-color; }

.after,
.before {
	position: relative; }

.after::after,
.before::before {
	content: '';
	position: absolute; }


$fwList: 300 400 500 600 700 800;
@each $fontWeight in $fwList {
	.fw-#{$fontWeight} {
		font-weight: $fontWeight; } }

.h-line {
	background-image: url('../img/title-detail.png');
	background-repeat: no-repeat;
	background-position: bottom center;
	background-size: contain;
	padding-bottom: 2%; }

.c-orange {
	color: $orange; }

.nav-padding {
	padding-top: 9.625rem; }

.title {
	font-size: 1.75rem;
	font-weight: 600;
	margin-bottom: 1.875rem;
	&.title-big {
		font-size: 2.1875rem; } }


.collapse-contact {
	background-color: $gray-2;
	border-radius: 0.375rem;
	min-height: 4.375rem;
	margin-top: 0.625rem; }


.status-online {
	width: 0.625rem;
	height: 0.625rem;
	border-radius: 50%;
	background-color: rgba($green, .58);
	box-shadow: 0 0 0 0.25rem rgba($bl, .04);
	display: inline-block; }

.box-online {
	padding-left: 2.0625rem;
	font-size: 0.875rem;
	font-weight: 600;
	position: relative;
	text-align: left;
	.status-online {
		position: absolute;
		top: 0.3125rem;
		left: 0.5rem; } }


.required {
	position: relative;
	&::before {
		content: '*';
		position: absolute;
		top: -0.3125rem;
		right: -0.5625rem;
		color: $red; } }

.box-info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	min-height: 4.75rem;
	padding-left: 2.0625rem;
	position: relative;
	text-align: left;
	&::before {
		content: '';
		position: absolute;
		top: 0.125rem;
		left: 0;
		background-image: url('../img/icons/info.png');
		opacity: .12;
		background-repeat: no-repeat;
		background-position: 0 0;
		width: 100%;
		height: 100%; }
	& > span {
		position: relative;
		z-index: 2; }
	.required {
		&::before {
			left: -0.875rem;
			top: -0.125rem; } }
	& &-text-big {
		font-weight: 700;
		font-size: 0.9375rem; }
	& &-text-small {
		font-weight: 600;
		font-size: 0.875rem; } }


.banner {
	margin: 2.5rem 0;
	position: relative;
	background-color: #eee;
	max-width: 17.1875rem;
	& &-item {
		width: 100%;
		border-radius: 0.375rem; } }
