$bem: '.btn';
.btn {
	display: flex;
	align-items: center;
	&-circle {
		width: 4.25rem;
		height: 4.25rem;
		border-radius: 50%;
		background: rgb(255,255,255);
		background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(221,221,221,1) 100%);
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all .2s;
		color: $gray-3;
		box-shadow: 0 0 0 0.375rem rgba($wh, .06);
		.icon {
			width: 1.875rem;
			height: 1.875rem; }
		&:hover {
			color: $orange;
			box-shadow: 0 0 0 0.375rem rgba($wh, .1), 0 0 0.375rem 0.375rem rgba($bl, .1); } }
	& &-icon {
		box-shadow: 0 0 0.9375rem 0 rgba($bl, .1);
		&,
		&-hold, {
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			background-color: $wh;
			width: 3.375rem;
			height: 3.375rem;
			border-radius: 50%;
			transition: all .25s; }
		.icon {
			width: 1.25rem;
			height: 1.25rem;
			color: $bl-3;
			transition: all .25s;
			&.icon-send {
				width: 1.375rem;
				height: 1.375rem; } }
		&-hold {}
		&-circle {
			top: -0.3125rem;
			left: -0.3125rem;
			width: calc(50% + 0.3125rem);
			height: 4rem;
			overflow: hidden;
			&,
			&::before {
				position: absolute; }
			&::before {
				top: 0;
				left: 0;
				width: 4rem;
				height: 4rem;
				content: '';
				border-radius: 50%;
				background-color: $orange;
				transition: all .25s; } } }
	& &-text {
		color: $bl-2;
		font-weight: 700;
		font-size: 1.375rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		margin-left: 1.375rem;
		transition: all .2s;
		&-part-1 {
			font-weight: 600;
			color: rgba($bl-2, .9);
			font-size: 0.5rem;
			text-transform: uppercase;
			transition: none; }
		&-part-2 {
			font-size: 1.0625rem; } }
	&:hover {
		#{$bem}-text {
			color: $orange;
			&-part-1 {
				color: rgba($bl-2, .9); } }
		#{$bem}-icon {
			.icon {
				color: $orange; } } }
	&-contact {
		padding-left: 0.3125rem;
		padding-right: 0;
		&--white {
			.btn-text {
				color: $wh; }
			&:hover {
				.btn-text {
					color: $wh;
					text-shadow: 0 2px 0px $orange; } } } } }


.btn.btn-primary {
	--bs-btn-bg: #{$orange};
	--bs-btn-border-color: #{$orange};
	--bs-btn-hover-bg: #{$orange};
	--bs-btn-hover-border-color: #{$orange};
	--bs-btn-active-bg: #{$orange};
	--bs-btn-active-border-color: #{$orange};
	--bs-btn-focus-shadow-rgb: 232, 139, 24; }
